<template>
  <nav
    class="navbar navbar-expand-lg navbar-dark bg-primary"
    v-if="$route.path !== '/login' && $route.path !== '/register'"
  >
    <router-link class="navbar-brand" to="/">VueChat</router-link>
    <button
      class="navbar-toggler"
      type="button"
      data-toggle="collapse"
      data-target="#navbarSupportedContent"
      aria-controls="navbarSupportedContent"
      aria-expanded="false"
      aria-label="Toggle navigation"
    >
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="navbar-nav ml-auto">
        <li class="nav-item active">
          <router-link class="nav-link" to="/">Home</router-link>
        </li>
        <li class="nav-item">
          <router-link class="nav-link" to="/login" v-if="!isLoggedIn">Login</router-link>
        </li>
        <li class="nav-item">
          <router-link class="nav-link" to="/register" v-if="!isLoggedIn">Register</router-link>
        </li>
        <li class="nav-item">
          <router-link class="nav-link" to="/chat" v-if="isLoggedIn">Chat</router-link>
        </li>
        <li class="nav-item">
          <router-link class="nav-link" to="/profile" v-if="isLoggedIn">Profile</router-link>
        </li>
        <li class="nav-item">
          <a class="nav-link" @click.prevent="logoutUser" v-if="isLoggedIn">Logout</a>
        </li>
      </ul>
    </div>
  </nav>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "Navbar",
  computed: {
    ...mapGetters(["isLoggedIn", 'socket']),
  },
  methods: {
    ...mapActions(["logout"]),
    logoutUser() {
      this.socket.emit('offline', localStorage.getItem('userId'))
      this.logout();
    },
  },
};
</script>

