import axios from 'axios'
import router from '../../router'
const API_URL = '/api/users'

const state = {
    token: localStorage.getItem('token') || '',
    user: {},
    status: '',
    error: null
}

const getters = {
    isLoggedIn: state => !!state.token,
    authState: state => state.status,
    user: state => state.user,
    error: state => state.error
}

const actions = {
    // Login
    async login({ commit }, user) {
        commit('authRequest')
        try {
            let res = await axios.post(`${API_URL}/login`, user)
            if (res.data.success) {
                const token = res.data.token
                const user = res.data.user
                // Set token into localStorage
                localStorage.setItem('token', token)
                localStorage.setItem('userId', user._id)

                // Set axios defaults
                axios.defaults.headers.common.Authorization = token

                commit('authSuccess', token, user)
            }
            return res
        } catch (err) {
            commit('authError', err)
        }
    },
    async register({ commit }, userData) {
        commit('registerRequest')
        try {
            let res = await axios.post(`${API_URL}/register`, userData)
            if (res.data.success !== undefined) {
                commit('registerSuccess')
            }
            return res
        } catch (err) {
            commit('registerError', err)
        }
    },
    // Get user profile
    async getProfile({ commit }) {
        commit('profileRequest')
        let res = await axios.get(`${API_URL}/profile`)
        commit('userProfile', res.data.user)
        return res
    },
    // Logout user
    async logout({ commit }) {
        await localStorage.removeItem('token')
        commit('logout')
        delete axios.defaults.headers.common.Authorization
        router.push('/login')
        return
    }
}

const mutations = {
    authRequest(state) {
        state.error = null
        state.status = 'loading'
    },
    authSuccess(state, token, user) {
        state.error = null
        state.token = token
        state.user = user
        state.status = 'success'
    },
    authError(state, err) {
        state.error = err.response.data.msg
    },
    registerRequest(state) {
        state.error = null
        state.status = 'loading'
    },
    registerSuccess(state, token, user) {
        state.error = null
        state.status = 'success'
    },
    registerError(state, err) {
        state.error = err.response.data.msg
    },
    logout(state) {
        state.error = null
        state.status = ''
        state.token = ''
        state.user = {}
    },
    profileRequest(state) {
        state.status = 'loading'
    },
    userProfile(state, user) {
        state.user = user
    },
}

export default {
    state,
    getters,
    actions,
    mutations
}